.skills-page {
  padding: 10px 70px;
  margin-left: 30px;
}

.skills-heading {
  margin-top: 50px;
  font-size: 2.5rem;
  margin-left: 20px;
  margin-bottom: 4px;
}

.pins-text {
    margin-left: 20px;
    color: grey;
    font-size: 15px;
    margin-bottom: 20px;
    margin-top: 0px;
}

.skills-masonry-grid {
  display: flex;
  margin-left: -20px; /* gutter size offset */
  width: auto;
}

.skills-masonry-column {
  padding-left: 20px; /* gutter size */
  background-clip: padding-box;
}

.skill-pin {
  position: relative;
  border-radius: 16px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.2s ease;
  margin-bottom: 50px;
}

.skill-image {
  width: 100%;
  display: block;
  border-radius: 16px;
  border: 1px solid #b3b0b0;
}

.skill-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(236, 131, 148, 0.427); /* light pink translucent */
  opacity: 0;
  transition: opacity 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.skill-pin:hover .skill-overlay {
  opacity: 1;
}

.skill-name {
  font-size: 1.2rem;
  font-weight: bold;
  margin: 0;
  color: #333;
}

.skill-type {
  font-size: 0.9rem;
  margin-top: 5px;
  color: #555;
}

.welcome-bar {
  background-color: #efefef;
  border-radius: 15px;
  padding: 12px 24px;
  color: #555;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  width: calc(100% - 80px); 
  max-width: 1000px;
}

.pin-label {
  position: absolute;
  padding: 15px 15px;
  font-size: 1rem;
  font-weight: bold;
}

.name-label {
  top: 10px;
  left: 10px;
  color: white;
}

.type-label {
  top: 10px;
  right: 10px;
  background-color: #e1072b; /* Pinterest red-pink */
  color: white;
  border-radius: 10px;
}

/* The sub options for filter */
.filter-dropdown {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end; /* aligns buttons to the right */
  gap: 10px;
  margin-right: 50px; /* 10px from the right */
  margin-top: -10px; /* optional: adjusts vertical spacing */
  margin-bottom: 30px;
}

.filter-option {
  background-color: #f7dee3;
  border: none;
  padding: 10px 15px;
  border-radius: 12px;
  font-size: 14px;
  color: #333;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.filter-option:hover {
  background-color: #f5afbd;
}

.filter-option.selected {
  background-color: #f093ac; /* darker pink when selected */
  color: white;
}

/* filter or tldr option */
.skills-toggle-buttons {
  display: flex;
  gap: 10px;
  margin-left: 800px; 
  margin-top: -50px;
  margin-bottom: 20px;
}

.skills-toggle-buttons button {
  color: #666;
  letter-spacing: 0.03em;
  border: none;
  padding: 15px 15px;
  font-size: 15px;
  background-color: white;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.skills-toggle-buttons button.active {
  text-decoration: underline;
  text-underline-offset: 10px;
  border: none;
  font-weight: 600;
}

.skills-toggle-buttons button:not(.active):hover {
  background-color: #f8dfe4;
}

.skills-text {
  margin-left: 20px;
}